
* {margin: 0; padding: 0
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* text-align: center; */
  color: #2c3e50;
  /* margin-top: 60px; */

  display: -webkit-box;

  display: -ms-flexbox;

  display: flex;
  min-height: 100vh;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.site-content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  background: #fff;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  min-width: 200px;
  max-width: 980px;
  width: 100%;
  margin: 0 auto;
  padding: 45px;
}

body {
  background: #e2e2e2;
}
.header {
  height: 40px;
  line-height: 40px;
  background: #fff;
  margin-bottom: 20px;
  text-align: center;
}
.header .nav a {
    color: #778087;
    font-size: 18px;
    font-weight: 500;
    text-decoration: none;
    padding: 0 8px;
}
.header .nav a:first-child {
      margin-right: 10px;
}
.header .nav a.active {
      border-bottom: 2px solid #778087;
}

.footer {
  text-align: center;
  padding: 10px;
}

.main {
  background: #fff;
}
.main .item {
    padding-bottom: 5px;
}
.main .item .title > a {
      color: #778087;
      text-decoration: none;
}
.main .item .date {
      float: right;
      color: #778087;
}

.about {
  margin: 0 auto;
  padding: 45px;
  background: #fff;
  text-align: center;
}
.about .contact {
    margin-top: 15px;
}
.about .contact img {
      width: 32px;
      height: 32px;
}
.about .contact img:hover {
        opacity: 0.5;
}

@media (max-width: 767px) {
.markdown-body {
    padding: 15px;
}
}
/* Make clicks pass-through */
#nprogress {
  pointer-events: none;
}
#nprogress .bar {
  background: #29d;

  position: fixed;
  z-index: 1031;
  top: 0;
  left: 0;

  width: 100%;
  height: 2px;
}
/* Fancy blur effect */
#nprogress .peg {
  display: block;
  position: absolute;
  right: 0px;
  width: 100px;
  height: 100%;
  -webkit-box-shadow: 0 0 10px #29d, 0 0 5px #29d;
          box-shadow: 0 0 10px #29d, 0 0 5px #29d;
  opacity: 1.0;

  -webkit-transform: rotate(3deg) translate(0px, -4px);
          transform: rotate(3deg) translate(0px, -4px);
}
/* Remove these to get rid of the spinner */
#nprogress .spinner {
  display: block;
  position: fixed;
  z-index: 1031;
  top: 15px;
  right: 15px;
}
#nprogress .spinner-icon {
  width: 18px;
  height: 18px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;

  border: solid 2px transparent;
  border-top-color: #29d;
  border-left-color: #29d;
  border-radius: 50%;

  -webkit-animation: nprogress-spinner 400ms linear infinite;
          animation: nprogress-spinner 400ms linear infinite;
}
.nprogress-custom-parent {
  overflow: hidden;
  position: relative;
}
.nprogress-custom-parent #nprogress .spinner,
.nprogress-custom-parent #nprogress .bar {
  position: absolute;
}
@-webkit-keyframes nprogress-spinner {
  0%   { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}
@keyframes nprogress-spinner {
  0%   { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

